.rui-collapse-header {
  display: flex;
}
.rui-collapse-left {
  flex: 1;
}
.rui-collapse-right {
  display: flex;
  align-items: center;
  flex-shrink: 0;
}
.rui-collapse-btn {
  user-select: none;
  cursor: pointer;
  color: #1890FF;
  margin-left: 12px;
}
.rui-collapse-body {
  overflow: hidden;
}
.rui-collapse-legacy {
  overflow: hidden;
}
.rui-collapse-legacy-active {
  transition: height 10s, opacity 10s !important;
}
.rui-collapse-content-hide {
  display: none;
}
